<template>
    <HeadTabbar class="headtabbar">
        <span v-for="item, index in selects" :class="{ active: index === currentindex }" :link="links[index]"
            @click="setindex(index)">{{ item }}</span>
    </HeadTabbar>

</template>
<script>
import HeadTabbar from 'components/common/headtabbar/HeadTabbar'
export default {
    name: 'maintabbar',
    data() {
        return {
            selects: ['登录', '我的', '关于'],
            links: ['/login', '/me', '/about'],
            currentindex: 0
        }
    },
    created(){
        this.$router.push('/login')
    },
    methods: {
        setindex(index) {
            this.currentindex = index
            switch (index) {
                case 0: this.$router.push('/login')
                    break
                case 1: this.$router.push('/me')
                    break
                case 2: this.$router.push('/about')
                    break
            }
        }
    },
    components: {
        HeadTabbar
    }
}
</script>
<style scpoed lang="less">
.headtabbar {
    background-color: aqua;

    span {
        cursor: pointer;
        color: #fff;
        flex: 1;
    }

    .active {
        color: red;
        border-bottom: 2px solid red;
    }

}
</style>



