<template>
    <div class="toast" v-if="ishow">
        {{message}}
    </div>
</template>
<script>
export default {
    name: 'toast',
    data(){
        return {
            ishow:false,
            message:'你好，这里是toast'
        }
    },
    methods:{
        show(message='提示文本',time=2000){
            this.ishow = true
            this.message = message
            setTimeout(()=>{
                this.ishow = false
            },time)
        }
    }
}
</script>
<style scoped>
.toast{
    font-size: 18px;
    padding:5px;
    background-color: rgba(100, 100, 100, .75);
    color:#fff;
    position: fixed;
    top:50%;
    left:50%;
    z-index: 9;
    border-radius: 5px;
    transform: translate(-50%,-50%);
}
</style>


