import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

const Login = ()=>import('views/headpage/HeadPage')
const Me = ()=>import('views/mepage/MePage')
const About = ()=>import('views/aboutpage/AboutPage')


const routes = [
  {
    path: '/',
    component:Login,
    redirect:'/login'
  },
  {
    path: '/login',
    component:Login
  },
  {
    path: '/me',
    component:Me
  },
  {
    path: '/about',
    component:About
  }
]

export default new Router({
  routes,
  mode:'hash'
})




