<template>
    <div class="headtabber">
        <slot></slot>
    </div>
</template>
<script>
export default {
    name: 'headtabbar'
}
</script>
<style scoped>
.headtabber{
    width:100%;
    height:44px;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    border-bottom: 2px solid rgba(100, 100, 100, .2);
    display: flex;
    padding:0 5px;
    justify-content: space-around;
    text-align: center;
    line-height: 44px;
    z-index:99;
}
</style>


